import { Headers, Http } from '@angular/http';

import {Product} from './Model/product'; 



@Injectable()
export class ProductService {

  private productUrl = 'https://www.googleapis.com/books/v1/volumes?q=floresdelmal&filter=full';  // URL to web api

 

  products: Product[] = [
    //{Id: 6, Promotion:true,  Name: '( $2400 !!)', Description: ': *2 (60/)  140/   HFP (270mg)C(15mg)(15mg) :300MG*60 :  2  2 2~3   HFPCNS-15492140/', Price:2400, Photo:'app/Services/Model/Photos/itemE.jpg'}, 
    {Id: 1, Name: '', Description: 'HFP-Chitosan(5730876)FDA(2030950)', Price: 3600, Photo:'/assets/Photos/itemA.jpg'}, 
    {Id: 2, Name: '', Description: ' HFP-ChitosanDHAB   360 mg60  31', Price: 1500, Photo:'/assets/Photos/cart.jpg'},  
    {Id: 3, Name: '', Description: '++ ', Price: 1800, Photo:'/assets/Photos/itemC.jpg'}, 
    {Id: 4, Name: '', Description: 'HFP-Chitosan800 ppmCNS-15492', Price: 200, Photo:'/assets/Photos/itemF.jpg'}, 
    //{Id: 7, Name: '', Description: '5  SGSSPA100%', Price: 800, Photo:'app/Services/Model/Photos/itemH.png'}, 
    {Id: 5, Name: '(11)', Description: '               3/', Price: 90, Photo:'/assets/Photos/itemG.jpg'}, 
  
  ]

  constructor(
    private http: Http
  ) { }

  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error);
    return Promise.reject(error.message || error);
  }

  getProducts(): Promise<any> {
    return this.http.get(this.productUrl)
      .toPromise()
      .then(response =>
        response.json())
      .catch(this.handleError);
  }

  getSamples() : Product[]{

      return this.products; 
  }



}
